*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: monospace;
}
header{
    width: 100vw;
    height: 120px;
   display: flex;
   background-color: white;
   justify-content: space-around;

}
body{
    width: 100vw;
    height: 100vh;
}
main{
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: max-content;
    flex-direction: column;
    gap: 5rem;
}

.Logo{
    width: 10%;
    align-items: center;
    height: 100%;
}
.info{
    display: flex;
    gap: 10%;
    align-items: center;
    width: 40%;
    justify-content: right;
}
.infos_itens{
    font-weight: 500;
    font-size: 16px;
    cursor: pointer;
}
.text_frame{
    width: 70%;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5px;
    
}
.text_frame span{
    font-size: 28px;
    flex-wrap: wrap;
    width: 100%;
  
}
.text_frame span::after{
    content: "";
    background: black;
    padding: 1.5px;
    position: relative;
    bottom: 5px;
    animation: bar .8s  infinite;
}
.text_frame p{
    margin-top: 15px;
}

.skills{
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
    padding: 16px;
}

.card_body{
    width: 250px;
    height: 300px;
    -webkit-box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    -moz-box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    box-shadow: 11px 15px 10px 5px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    gap: 0.9rem;
}

.card_body span{
    flex: 1;
    text-align: center;
    font-size: 16px;
}
.coding{
    width: 100%;
    height: max-content;
    padding: 15px;
}
.coding_body{
    border-radius: 20px;
    width:40%;
    max-width: 500px;
    height: 500px;
    background: #10002b;
   
}

.repos{
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 5px; 
    overflow-y: scroll;
}
.repos li{
    color: black;
    list-style: none;
    padding: 10px;
    border-radius: 20px;
    background-color: white;
    cursor: pointer;
}
.repos li span{
    font-size: 16px;
    font-weight: 700;
}
.repos li p{
    font-size: 12px;
    font-weight: 400;
}
@keyframes bar {
    50%{
        opacity: 0;
    }
    
}